import PropTypes from 'prop-types';
import React, { useMemo, useRef } from 'react';

import { __t, LinkBlockKind } from '@mssgme/shared';
import { cls, getTransparentColor } from '@mssgme/helpers';
import { useForceUpdate, useMergedRefs } from '@mssgme/hooks';
import { LandingsPropTypes } from '@mssgme/ui';

import { useBlockTheme, useContrastingPageColor, useWebsiteMenu } from '../../hooks';
import { SmartButton } from '../../UI';

import styles from './PageMenu.scss';

const getClosestWidth = (node, threshold = 100) => {
    while (node) {
        const got = node.clientWidth;

        if (got >= threshold) {
            return got;
        }

        node = node.parentNode;
    }

    return 0;
};

export const PageMenu = ({
    as: Tag = 'nav',
    website,
    page,
    interactive = true,
    isViewMode = false,
    isMobileMode,
    type = 'fixed',
    ...rest
}) => {
    const homePageId = website.homePageId;
    const colors = useContrastingPageColor();
    const { items } = useWebsiteMenu(website);
    const { style, invertedStyle } = useMemo(() => {
        const backgroundColor = getTransparentColor(colors.backgroundColor, 0.99).toRgbString();
        const color = colors.color;
        const style = { backgroundColor, color };
        const invertedStyle = { backgroundColor: color, color: backgroundColor };

        return {
            style,
            invertedStyle,
        };
    }, [type, colors]);
    const forceUpdate = useForceUpdate();
    const ref = useRef();
    const tagRef = useMergedRefs([ref, forceUpdate]);
    const block = useBlockTheme({ kind: LinkBlockKind, theme: {} });
    const pageId = page._id;
    const links = useMemo(
        () => {
            const titlesMap = Object.fromEntries(
                website.pages.map((page) => [page._id, page.name])
            );

            return items.filter(({ enabled }) => enabled).map(({ page, label }) => {
                const isHomePage = page === homePageId;

                return {
                    page,
                    active: page === pageId,
                    title: label || (isHomePage ? __t('homePage') : titlesMap[page]),
                };
            });
        },
        [website.pages, items, pageId]
    );

    if (links.length <= 1) {
        return null;
    }

    // can't use css breakpoints here, but probably should add listener to make it responsive
    const isCompacted = isMobileMode || (getClosestWidth(ref.current) < 600);
    const isFixedButton = isCompacted && type === 'fixed';
    const className = {
        [styles.root]: true,
        [styles.fixed]: isFixedButton,
        [styles.compact]: isCompacted,
        [styles.disabled]: !interactive,
    };

    return (
        <Tag
            key={pageId + isFixedButton}
            ref={tagRef}
            id="page_menu"
            style={isFixedButton ? null : style}
            {...cls(className, rest)}
        >
            <div className={styles.header}>
                {website.name}
            </div>

            <input id="menu-toggle" type="checkbox" className={styles.toggle} disabled={!interactive} />

            <label
                htmlFor="menu-toggle"
                className={styles.buttonContainer}
                style={isFixedButton ? invertedStyle : style}
            >
                <div className={styles.button}></div>
            </label>

            <ul className={styles.menu} style={style}>
                {links.map((link) => (
                    <li key={link.page} className={styles.item}>
                        <SmartButton
                            interactive={interactive}
                            isViewMode={isViewMode}
                            id={link.page}
                            link={link}
                            active={link.active}
                            width={block.theme.width}
                            icon={block.theme.icon}
                            urlSubtitle={block.theme.urlSubtitle}
                            style={style}
                        />
                    </li>
                ))}
            </ul>
        </Tag>
    );
};

PageMenu.propTypes = {
    as: PropTypes.elementType,
    website: LandingsPropTypes.website.isRequired,
    page: LandingsPropTypes.page.isRequired,
    interactive: PropTypes.bool,
    isViewMode: PropTypes.bool,
    isMobileMode: PropTypes.bool,
    type: PropTypes.oneOf(['fixed', 'header']),
};
