import React, { useMemo } from 'react';
import { useTranslation } from 'react-i18next';
import { useHistory } from 'react-router-dom';

import { randomSlug } from '@mssgme/shared';
import { useActions, useEventCallback, useNavigationUtils, useWebsitePageBySlugs, useWebsites } from '@mssgme/hooks';
import { SelectWebsiteField } from '@mssgme/ui';
import { PageEditForm } from '../../../../UI';

const NEW_TAG = 'new';

export function PageClone() {
    const history = useHistory();
    const websites = useWebsites();
    const { t } = useTranslation('landings');
    const { generateUrl } = useNavigationUtils();
    const { websiteId, pageId, page, website } = useWebsitePageBySlugs();
    const { api } = useActions();
    const root = generateUrl('../../..', { useMatchUrl: true });

    const options = useMemo(() => {
        const unnamed = t('labels.unnamedWebsite');
        const toOption = ({ _id, name, finalUrl }) => {
            const label = _id === websiteId
                ? t('Page.Edit.Clone.fields.target.options.this', { label: name || unnamed })
                : name || unnamed;

            return {
                url: finalUrl,
                value: _id,
                label,
            };
        };

        return [
            {
                label: t('Page.Edit.Clone.fields.target.groups.existing'),
                options: [
                    toOption(website),
                    ...websites.filter(({ _id }) => _id !== websiteId).map(toOption),
                ],
            },
            {
                label: t('Page.Edit.Clone.fields.target.groups.other'),
                options: [
                    toOption({ _id: NEW_TAG, name: t('Page.Edit.Clone.fields.target.options.new') }),
                ],
            },
        ];
    }, [website, websites]);

    const handleSubmit = useEventCallback(async ({ website, name, slug }) => {
        const result = await api.pages.clone({
            websiteId,
            pageId,
            payload: {
                targetWebsiteId: website || '',
                enabled: true,
                slug: slug + '-' + randomSlug(5),
                name: t('Page.Edit.Clone.copyName', { name }),
            },
        });

        setTimeout(() => history.push(`${root}/${result.website}/${result._id}/edit/pages`), 200);

        return false;
    });

    return (
        <PageEditForm
            initialDirty
            title={t('Page.Edit.Clone.heading')}
            websiteId={websiteId}
            page={page}
            initialValues={{ website: websiteId }}
            backPath={generateUrl('..')}
            onSubmit={handleSubmit}
        >
            <SelectWebsiteField
                required
                name="website"
                label={t('Page.Edit.Clone.fields.target.label')}
                options={options}
            />
        </PageEditForm>
    );
}
