import React, { useCallback, useMemo } from 'react';

import Select from 'react-select';

const selectThemeOverride = (theme) => ({
    ...theme,
    colors: {
        ...theme.colors,
        primary: '#007aff',
        neutral20: '#c7c7cc',
        neutral30: '#8e8e93',
    },
});

const filterValue = (multiple, options, value) =>
    multiple
        ? (value && value.length && filterValues(options, value)) || []
        : findOneValue(options, value);

const findOneValue = (options, value) => {
    for (const groupOrOption of options) {
        const nested = groupOrOption.options;

        if (nested) {
            const found = findOneValue(nested, value);

            if (found) {
                return found;
            }
        } else if (groupOrOption.value === value) {
            return groupOrOption;
        }
    }
};

const filterValues = (options, values, target = []) => {
    if (!options.length) {
        return target;
    }

    for (const groupOrOption of options) {
        const nested = groupOrOption.options;

        if (nested) {
            filterValues(nested, values, target);
        } else if (values.includes(groupOrOption.value)) {
            target.push(groupOrOption);
        }
    }

    return target;
};

export function StyledSelectInput({ value, multiple, disabled, onChange, id, ...rest }) {
    const handleChange = useCallback((changed, { action, removedValue }) => {
        switch (action) {
            case 'remove-value':
            case 'pop-value':
                if (removedValue.isFixed) {
                    return;
                }

                break;
        }

        const normalized = multiple
            ? changed
                ? changed.map((item) => item.value)
                : []
            : changed?.value || undefined;

        onChange(normalized);
    }, [onChange, multiple]);
    const defaultValue = useMemo(() => filterValue(multiple, rest.options, value), [multiple, rest.options, value]);

    return (
        <Select
            theme={selectThemeOverride}
            isClearable={false}
            isSearchable={false}
            isDisabled={disabled}
            isMulti={!!multiple}
            inputId={id}
            menuPlacement="auto"
            menuPortalTarget={document.body}
            value={defaultValue}
            defaultValue={defaultValue}
            onChange={handleChange}
            {...rest}
        />
    );
}

StyledSelectInput.propTypes = Select.propTypes;
