import { ACTION_CREATE, ACTION_EDIT, ACTION_DELETE } from '../../request';
import { Voter } from '../voter';

export class PageTemplatesVoter extends Voter {
    supports(request) {
        return request.resource === 'page-templates';
    }

    vote(request) {
        if (request.includes(ACTION_CREATE)) {
            return this.canCreate(request);
        }

        if (request.includes(ACTION_EDIT)) {
            return this.canEdit(request);
        }

        if (request.includes(ACTION_DELETE)) {
            return this.canDelete(request);
        }

        return true;
    }

    /**
     * @param {AccessPermissionRequest} request
     * @returns {boolean}
     */
    canCreate(request) {
        const count = request.workspace.quantities.templates || 0;

        switch (request.role) {
            case 'unlimited':
                return count < 20;

            default:
                return false;
        }
    }

    /**
     * @param {AccessPermissionRequest} request
     * @returns {boolean}
     */
    canDelete(request) {
        return this.canEdit(request);
    }

    canEdit(request) {
        if (!request.subject?._id) {
            return true;
        }

        return request.isBy(request.subject.author);
    }
}
