import React from 'react';

import { removeTrailingSlash } from '@mssgme/shared';

export const makeCoreRouter = ({ pageSlug, path, isBackOverriddenBack }) => {
    const toAbsolutePath = (pageSlug, ...parts) => {
        const joined = removeTrailingSlash(parts.reduce((acc, part) => {
            if (part ?? false) {
                return acc + '/' + removeTrailingSlash(part, { lead: true });
            }

            return acc;
        }, ''));

        return joined.replace('/:pageSlug', pageSlug ? `/${pageSlug}` : '');
    };
    const base = toAbsolutePath(pageSlug, path);
    const entry = base || '/';

    return {
        toEntry: () => entry,
        toHome: () => entry,
        toPage: (pageSlug) => toAbsolutePath(pageSlug, path),
        product: {
            toView: (productSlug = ':productSlug') => `${base}/p/${productSlug.toLowerCase()}`,
        },
        collection: {
            toView: (collectionSlug = ':collectionSlug') => `${base}/c/${collectionSlug.toLowerCase()}`,
        },
        orders: {
            toList: () => `${base}/orders`,
            toView: (orderId = ':orderId', basePath) =>
                `${basePath ? toAbsolutePath(pageSlug, basePath) : base}/orders/${orderId}`,
            toStatus: (orderId = ':orderId', status = ':status(paid|failed)') =>
                `${base}/orders/${orderId}/payment/${status}`,
        },
        isBackOverriddenBack,
    };
};

export const CoreContext = React.createContext({
    path: '/',
    router: makeCoreRouter({ path: '/' }),
    setEntry: () => {},
});
