import PropTypes from 'prop-types';
import React from 'react';
import { Link } from 'react-router-dom';

import { __t } from '@mssgme/shared';
import { BlockListItemPreview, LandingsPropTypes } from '@mssgme/ui';
import { FileIcon, HomeIcon } from '@mssgme/icons';
import { useCoreContext } from '@mssgme/userpage-core';

PageItem.propTypes = {
    website: LandingsPropTypes.website.isRequired,
    page: LandingsPropTypes.page.isRequired,
    activeId: PropTypes.string,
};

export function PageItem({ website, page, activeId }) {
    const { router } = useCoreContext();
    const isHomePage = website.homePageId === page._id;
    const isActive = activeId === page._id;
    const isEnabled = isHomePage || page.enabled;
    const attributes= isHomePage ? {
        label: __t('homePage'),
        labelIcon: HomeIcon,
    } : {
        label: page.name,
        labelIcon: FileIcon,
        preview: `/${page.slug}`,
    };

    return (
        <BlockListItemPreview
            iconSplit
            tag={Link}
            iconColor={isEnabled ? isActive ? '#33f' : '#333' : '#aeaeae'}
            to={router.toPage(page._id)}
            more={{
                to: {
                    pathname: `${router.toPage(page._id)}/pages/settings`,
                    state: { backPath: `${router.toPage(page._id)}/pages` },
                },
            }}
            {...attributes}
        />
    );
}
