import cn from 'classnames';
import PropTypes from 'prop-types';
import React from 'react';

import { __t, isMatchedById } from '@mssgme/shared';
import { FileIcon, HomeIcon } from '@mssgme/icons';
import { Badge, LandingsPropTypes } from '@mssgme/ui';

import styles from './MenuItem.scss';

export const MenuItem = ({ value, className, textClassName, context: website }) => {
    const isHomePage = website.homePageId === value.page;
    const page = website.pages.find(isMatchedById(value.page));
    const label = value.label || (isHomePage ? __t('homePage') : page?.name);
    const Icon = isHomePage ? HomeIcon : FileIcon;

    return (
        <div className={className}>
            <Icon size={22} />
            <div className={cn(styles.label, textClassName)}>{label?.replace(/\n/g, ' ')}</div>
            {!isHomePage && (
                <div className={styles.details}>
                    <div className={styles.slug}>/{page?.slug}</div>
                    <Badge dot badgeStyle={value.enabled ? 'success' :  'flat'} />
                </div>
            )}
        </div>
    );
};

MenuItem.propTypes = {
    context: LandingsPropTypes.website.isRequired,
    value: PropTypes.object.isRequired,
    onEdit: PropTypes.func.isRequired,
    onDelete: PropTypes.func.isRequired,
    className: PropTypes.string,
    textClassName: PropTypes.string,
};
