import PropTypes from 'prop-types';
import React, { forwardRef, useMemo } from 'react';
import { useMeasure } from 'react-use';

import { cls } from '@mssgme/helpers';
import { useMergedRefs } from '@mssgme/hooks';

import styles from './Scalable.scss';

export const Scalable = forwardRef(function Scalable(
    { isScaled, isAnimated, scale = 1, children, style: baseStyle, ...rest },
    forwarded
) {
    const [ref, { height }] = useMeasure();
    const mergedRefs = useMergedRefs([forwarded, ref]);
    const actualScale = isScaled ? scale : 1;
    const style = useMemo(
        () => ({
            ...baseStyle,
            transform: `scale(${actualScale})`,
            marginBottom: -((height / actualScale) * (1 - actualScale)),
        }),
        [height, actualScale, baseStyle]
    );

    return (
        <div
            ref={mergedRefs}
            style={style}
            {...cls([styles.root, !!height && styles.ready, isAnimated && styles.animated], rest)}
        >
            {children}
        </div>
    );
});

Scalable.propTypes = {
    isScaled: PropTypes.bool,
    isAnimated: PropTypes.bool,
    scale: PropTypes.number,
    style: PropTypes.object,
};
