import PropTypes from 'prop-types';
import React, { useCallback, useMemo } from 'react';
import { useTranslation } from 'react-i18next';

import { prettifyUrl } from '@mssgme/shared';
import { LandingsPropTypes } from '../../../domainTypes';
import { StyledSelectInput } from '../StyledSelectInput';

import styles from './SelectWebsiteInput.scss';

const formatOptionLabel = (option, { context }) => (
    <div className={styles.option}>
        <div>{option.label}</div>

        {context === 'menu' && option.url && (
            <div className={styles.url}>{prettifyUrl(option.url)}</div>
        )}
    </div>
);
const formatGroupLabel = (group) => (
    <div className={styles.group}>
        <div>{group.label}</div>
    </div>
);

export function SelectWebsiteInput({ websites, makeLabel, ...rest }) {
    const { t } = useTranslation('ui');
    const options = useMemo(() => websites?.map((website) => {
        const label = website.name || t('labels.unnamedWebsite');

        return {
            value: website._id,
            url: website.finalUrl,
            label: makeLabel?.(website, label) || label,
        };
    }), [websites, makeLabel]);
    const getOptionValue = useCallback((option) => option.value);

    return (
        <StyledSelectInput
            isSearchable
            isClearable={!rest.required}
            options={options}
            getOptionValue={getOptionValue}
            formatOptionLabel={formatOptionLabel}
            formatGroupLabel={formatGroupLabel}
            {...rest}
        />
    );
}

SelectWebsiteInput.propTypes = {
    ...StyledSelectInput.propTypes,
    websites: PropTypes.arrayOf(LandingsPropTypes.website.isRequired),
};
