import { useEffect, useRef } from 'react';

export const useBlockAddedEvent = (pageId, allBlocks, onEvent) => {
    const all = allBlocks.length;
    const counts = useRef();
    const fnRef = useRef();
    const tags = [pageId, all];

    fnRef.current = onEvent;

    if (!counts.current) {
        counts.current = tags;
    }

    useEffect(() => {
        const [prevId, prevAll] = counts.current;

        counts.current = tags;

        if (prevId === pageId && all > prevAll) {
            return fnRef.current(allBlocks[all - 1]);
        }
    }, tags);
};
