import cn from 'classnames';
import React, { useMemo } from 'react';
import { AnimatePresence } from 'framer-motion';
import { Provider, useSelector } from 'react-redux';
import { Redirect } from 'react-router-dom';

import { isMatchedById } from '@mssgme/shared';
import { useDelay, useNavigationUtils } from '@mssgme/hooks';
import { MatrixOverlay, ThemeContext, useIsMobile } from '@mssgme/ui';
import {
    BackgroundImage,
    BuyerActions,
    configureStore,
    CoreContextProvider,
    PageMenu,
    RendererContext,
    usePageData,
    usePageFrontendTranslations,
    useResolvedTheme,
    useWebsiteData,
} from '@mssgme/userpage-core';
import { BlockDeletionModal } from '../../UI';
import { LandingsBuilderRouter } from '../LandingsBuilderRouter';
import { LandingsEditorsRouter } from '../LandingsEditorsRouter';
import { LandingsEditorProductsRouter } from '../LandingsEditorProductsRouter';
import { useBuilderModes } from './useBuilderModes';
import { useBlocksMonitor } from './useBuilderHooks';
import { useRendererConfig } from './useRendererConfig';
import { useBlockRenderer } from './useBlockRenderer';
import { useViewportStyles } from './useVieportStyles';
import { SortableBlocksList } from './SortableBlocksList';
import { EmptyPage } from './EmptyPage';
import { Dashboard } from './Dashboard';
import { BuilderHeader } from './BuilderHeader';
import { SelectionArea } from './SelectionArea';
import { Scalable } from './Scalable';

import styles from './VisualBuilder.scss';

export function VisualBuilder() {
    const isMobile = useIsMobile();
    const website = useWebsiteData();
    const page = usePageData();
    const homePage = website.pages.find(isMatchedById(website.homePageId));
    const guideFinished = useSelector((state) => state.workspace.onboarding.editor.finished);
    const { isLoaded } = usePageFrontendTranslations(website.localization?.lang);
    const { generateUrl } = useNavigationUtils({ useMatchUrl: true });
    const { theme, style, resolvedTheme, align } = useResolvedTheme(homePage);
    const { baseUrl, isGuideMode, isViewMode, isDesktopMode } = useBuilderModes(page);
    const {
        ref: contentRef,
        isMatrix,
        isDisabledDashboard,
        isDeleting,
        isSorting,
        blocks,
        selection,
        toggleMatrix,
        handleSortEnd,
        handleSortStart,
        handleBlockEdit,
        handleBlockSelect,
        handleClearSelection,
        handleBlockToggle,
        handleAction,
        handleDeletion,
    } = useBlocksMonitor(website, page);
    const store = useMemo(() => configureStore({ website, page }), [website, page]);
    const isAnimated = useDelay(300); // do not animate first render
    const [ref, rendererConfig] = useRendererConfig({
        theme: resolvedTheme,
        isWebsitePublished: website.enabled,
        isAnimated,
        isMobile,
        isViewMode,
        blocks,
        selection,
    });
    const blockRenderer = useBlockRenderer(rendererConfig, store);
    const showContents = isViewMode || page.blocks.length;

    // const handleEditEmbed = useCallback(() => history.push(generateUrl(`settings/embedded`)), []);

    useViewportStyles(isDesktopMode && style);

    if (!(guideFinished || isGuideMode)) {
        return <Redirect to={generateUrl('guide')} />;
    }

    if (!isLoaded) {
        return null;
    }

    const menu = website.menu?.enabled && (
        <PageMenu
            as={Scalable}
            className={styles.fightZ}
            scale={rendererConfig.scale}
            isScaled={rendererConfig.isScaled}
            isAnimated={rendererConfig.isAnimated}

            website={website}
            page={page}
            interactive
            isViewMode
            isMobileMode={!isDesktopMode}
        />
    );

    const contents = (
        <div className={cn(styles.root, isViewMode && styles.viewMode)} id="visual-builder">
            <BuilderHeader key={page._id} isViewMode={isViewMode} page={page} website={website} />

            <div className={cn(styles.bgColor, isViewMode && styles.viewMode)} style={style}>
                {theme.backgroundImage && (
                    <BackgroundImage
                        className={styles.bgImage}
                        path={theme.backgroundImage}
                        opacity={theme.backgroundImageOpacity}
                    />
                )}
            </div>

            <div className={styles.layout}>
                <div className={cn(styles.content, isViewMode && styles.viewMode)} ref={contentRef}>
                    <SelectionArea
                        className={styles.contentInner}
                        style={align.container}
                        blocks={blocks}
                        active={!(isSorting || isViewMode)}
                    >
                        <SortableBlocksList
                            innerRef={ref}
                            style={align.inner}
                            draggable={!isViewMode && !handleSortEnd.pending}
                            config={rendererConfig}
                            onSortStart={handleSortStart}
                            onSortEnd={handleSortEnd.run}
                            onBlockClick={handleBlockSelect}
                            onBlockDoubleTap={handleBlockEdit}
                            onBlockToggle={handleBlockToggle}
                        >
                            {menu}
                        </SortableBlocksList>

                        {!showContents && (
                            <EmptyPage
                                isSinglePage={website.pages.length <= 1}
                                className={styles.emptyPage}
                                style={{ color: resolvedTheme.colors.text }}
                            />
                        )}

                        {/*{website.embeddedCode && (*/}
                        {/*    <div onClick={handleEditEmbed}>*/}
                        {/*        <EmbeddedCode*/}
                        {/*            code={website.embeddedCode}*/}
                        {/*            fit={!!website.embeddedFit}*/}
                        {/*            style={{ display: isSortMode ? 'none' : 'block', pointerEvents: 'none' }}*/}
                        {/*        />*/}
                        {/*    </div>*/}
                        {/*)}*/}

                        {/*{website.branding && <Branding page={page} style={{ pointerEvents: 'none' }} />}*/}
                    </SelectionArea>
                </div>
            </div>

            <Dashboard
                blocks={blocks}
                selected={selection.items}
                isDisabled={isDisabledDashboard}
                isFocusMode={isViewMode}
                isEmptyPage={!showContents}
                onClearSelection={handleClearSelection}
                onAction={handleAction}
            />

            <AnimatePresence>
                {isMatrix && <MatrixOverlay onTimeout={toggleMatrix} />}
                {isDeleting && <BlockDeletionModal handle={handleDeletion} count={selection.count} />}
            </AnimatePresence>

            <LandingsBuilderRouter />
            <LandingsEditorsRouter />
            <LandingsEditorProductsRouter />

            {isViewMode && (
                <Provider store={store}>
                    <div className={styles.actionsContainer}>
                        <div className={styles.actions}>
                            <BuyerActions style={{ zIndex: 11 }} />
                        </div>
                    </div>
                </Provider>
            )}
        </div>
    );

    return (
        <ThemeContext.Provider value={resolvedTheme}>
            <RendererContext.Provider value={blockRenderer}>
                <CoreContextProvider store={isViewMode && store} base={baseUrl} pageSlug={page._id}>
                    {contents}
                </CoreContextProvider>
            </RendererContext.Provider>
        </ThemeContext.Provider>
    );
}
