import PropTypes from 'prop-types';
import React, { useCallback, useLayoutEffect, useRef } from 'react';
import { set } from 'lodash';
import { useField, useFormikContext } from 'formik';
import { useTranslation } from 'react-i18next';

import { LandingsPropTypes, TextField, ToggleField } from '@mssgme/ui';
import { useWebsiteMenu } from '@mssgme/userpage-core';
import { ArrayField } from '../../../../UI';
import { MenuItem } from './MenuItem';

const getItemKey = (item) => item.page;

export const MenuItemsField = ({ website, ...props }) => {
    const formik = useFormikContext();
    const { t } = useTranslation('landings');
    const [{ value }] = useField(props.name);
    const { current: labels } = useRef({
        modal: {
            title: t('Website.Edit.Menu.itemFormHeading'),
        },
    });

    const { items, added, deleted } = useWebsiteMenu(website, value);
    const isHomePage = useCallback((item) => (item.page === website.homePageId) ? 0 : 1, [website.homePageId]);

    useLayoutEffect(() => {
        formik.resetForm(set(Object.assign({}, formik.values), props.name, items));
    }, [added, deleted]);

    if (added || deleted) {
        return null;
    }

    return (
        <ArrayField
            labels={labels}
            Item={MenuItem}
            maxItems={-1}
            getItemKey={getItemKey}
            getItemCollection={isHomePage}
            getItemSortable={isHomePage}
            context={website}
            value={items}
            isDeletable={false}
            {...props}
        >
            <ToggleField
                builderInput
                name="enabled"
                label={t('Website.Edit.Menu.fields.enabled.label')}
            />

            <TextField
                builderInput
                name="label"
                label={t('Website.Edit.Menu.fields.label.label')}
                placeholder={t('Website.Edit.Menu.fields.label.placeholder')}
                maxLength={250}
            />
        </ArrayField>
    );
};

MenuItemsField.propTypes = {
    website: LandingsPropTypes.website.isRequired,
    name: PropTypes.string.isRequired,
    initialEdit: PropTypes.bool,
};
